/* owl-carousel button style */
.owl-carousel {
    .owl-nav div {
        background: rgba(255, 255, 255, 0.8) none repeat scroll 0 0;
        height: 40px;
        left: 20px;
        line-height: 40px;

        font-size: 22px;
        color: #646464;
        opacity: 1;
        visibility: visible;
        position: absolute;
        text-align: center;
        top: 50%;
        transform: translateY(-50%);
        transition: all 0.3s ease 0s;
        width: 40px;
    }

    .owl-nav {
        div {
            &.owl-next {
                left: auto;
                right: -30px;

                i {
                    position: relative;
                    right: 0;
                    top: 1px;
                }
            }

            &.owl-prev {
                i {
                    position: relative;
                    right: 1px;
                    top: 0;
                }
            }
        }
    }

    &:hover {
        .owl-nav {
            div {
                opacity: 1;
                visibility: visible;

                &:hover {
                    color: #fff;
                    background: $theme-color4;
                }
            }
        }
    }
}


.btn {
    background: $btn_bg;
    border: 0;
    -moz-user-select: none;
    color: #fff;
    letter-spacing: 1px;
    padding: 15px 10px;
    min-width: 200px;
    position: relative;
    z-index: 1;
    margin: 1px;

    &:hover::before {
        transform: scaleX(1);
        color: #fff !important;
        z-index: -1;
    }

    &-primary:hover {
        background-color: #4043bc;
    }

    &.btn-link {
        min-width: unset;
        overflow: unset;
        color: #002e5b;
        padding: 0;
        display: block;
    }

    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #ec4683;;
        z-index: 1;
        border-radius: 5px;
        transition: transform 0.5s;
        transform-origin: 0 0;
        transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
        //
        transform: scaleX(0);


    }
}


// other
